
































































































































































































































































































































































































































































.flex-full-size
  display: flex
  flex-direction: column
  flex: 1
.desktop-settings-wrapper
  margin: 0
  background-color: var(--color-surface-secondary)
  ::v-deep .desktop-settings-content
    padding: 2rem
    padding-right: 3rem
.mobile-settings-wrapper
  flex: 1
  background-color: var(--color-surface-secondary)
  padding: 1.5rem
  .mobile-settings-content
    padding-right: 1rem
  .mobile-settings-categories
    .categories-title
      font-weight: bold
      font-size: 1.5rem
      margin-bottom: 1rem
    a
      color: var(--text-primary)
      display: flex
      justify-content: space-between
      align-items: center
      padding: .8rem 0
      border-bottom: 1px solid var(--color-divider)
      font-size: 1.2em
      &.subcategory
        font-size: 1em
        padding-left: 1em
      i
        font-size: .8rem
::v-deep section:not(:first-child) .section-title
  margin-top: 2rem
::v-deep .section-title
  font-weight: bold
  font-size: 1.5rem
  margin-bottom: 1rem
  border-bottom: 1px solid var(--color-text-primary)
::v-deep .settings-nav
  width: 25%
  background-color: var(--color-surface-primary)
  min-height: 80vh
  padding: 3.5rem 0
  .subcategory
    a
      padding-left: 2rem
      font-size: 0.9em
  a
    border: initial
::v-deep .nav-tabs
  border-bottom: none
