














































































.saving-in-progress, .successfully-saved, .failed-to-save
  $indicatorSize: 16px

  &:before
    content: ""
    background-size: $indicatorSize $indicatorSize
    width: $indicatorSize
    height: $indicatorSize
    display: block
    position: absolute
    top: 0
    bottom: 0
    right: -#{$indicatorSize + 12px}
    margin: auto
    z-index: 9

  &.saving-in-progress
    position: relative

    &:before
      background-image: var(--url-tail-spin)

  &.successfully-saved
    position: relative

    &:before
      background-image: url('/static/img/tick.svg')

  &.failed-to-save
    position: relative

    &:before
      background-image: url('/static/img/cross.svg')
